@import '../../../styles/variables.less';

.myModal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0,0,0, 0.5);
    z-index: 100;
}

.myModalContent {
    padding: 25px;
    /* background: white; */
    border-radius: 10px;
    min-width: 250px;
    background-color: @background-color;
}

.myModal.active {
    display: flex;
    justify-content: center;
    align-items: center;
}